.album {
    padding: 30px;
    border: 1px solid;
    border-radius: 15px;
    height: fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    background: #fff;
    position: relative;
    cursor: pointer;
    z-index: 10;
  
    &__user_name {
      font-weight: bold;
      font-size: 25px;
      margin-bottom: 15px;
    }
  
    &__container {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.5);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 0;
    }
  }